<template lang="pug">
  .not-found
    img(:src="'/static/img/404.png'")
    figcaption Что-то пошло не так, запрашиваемая страница не найдена(
    br
    figcaption {{$route.params.message}}

</template>

<script>
    export default {
      name: "PageNotFound"
    }
</script>

<style scoped>
  .not-found{
    width: 100%;
    min-width: 400px;
    padding: 30px 50px 50px;
    text-align: center;
  }
  img {
    width: 100%;
    max-width: 600px;
    text-align: center;
    margin-bottom: 20px;
  }
  figcaption {
    font-size: 18px;
    color: #21A45D;
  }
</style>



